import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { LiveDataComponentComponent } from '../live-data-component/live-data-component.component'
import { SetPointComponent } from '../set-point/set-point.component'

const routes: Routes= [
	{ path: '', redirectTo: '/live', pathMatch: 'full' },
	{ path: 'live',  component: LiveDataComponentComponent },
	{ path: 'setting',  component: SetPointComponent }
]

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
  	RouterModule
  ]
})
export class AppRoutingModule { }



