import { Component, OnInit } from '@angular/core';
import { DataService } from '../share/data.service';
import {SocketService} from '../share/socket.service'
@Component({
  selector: 'app-set-point',
  templateUrl: './set-point.component.html',
  styleUrls: ['./set-point.component.css']
})
export class SetPointComponent implements OnInit {
  public selected:any=0;
  constructor(public data:DataService,public io:SocketService) { 

  }

  ngOnInit() {

  }

  private UpdateSetPoint():void {
    console.log(this.data.setpoint.setPoint);
    this.io.socket.emit("SET_POINT" , this.data.setpoint.setPoint)
  }
}



