import { Injectable } from '@angular/core';
import { SocketService } from './socket.service'
@Injectable()
export class DataService {
  public sensor: SensorModel;
  public setpoint: SetPointModel;
  constructor(public io: SocketService) {
    this.sensor = new SensorModel();
    this.setpoint = new SetPointModel();

    console.log("[DataService] dataservice init");
    this.io.socket.on("SENSOR_DATA", (data: any) => {
      this.sensor.setValue(data.time, data.vpd, data.soil, data.temp, data.humi, data.light);
     // console.log(this.sensor)
    })

    this.io.socket.on("SET_POINT", (data: any) => {
      console.log('[DataService] Set Point\n ' + JSON.stringify(data));
      this.setpoint.setPoint = data;
    })
  }
}

class SensorModel {
  public time: string;
  public vpd: number;
  public soil: number;
  public temp: number;
  public humi: number;
  public light: number;

  constructor() {
    this.time = "00:00:00";
    this.vpd = 0.0;
    this.soil = 0.0;
    this.temp = 0.0;
    this.humi = 0.0;
    this.light = 0;
  }

  public setValue(time: string, vpd: number, soil: number, temp: number, humi: number, light: number): void {
    this.time = time;
    this.vpd = vpd;
    this.soil = soil;
    this.temp = temp
    this.humi = humi;
    this.light = light;
  }
}

class SetPointModel {
  public setPoint: any = [
    {
      "ch": 1,
      "vpd": [2200, 2250, false],
      "soil": [40, 60, false],
      "use": true
    },
    {
      "ch": 2,
      "vpd": [2200, 2250, false],
      "soil": [40, 60, false],
      "use": true
    },
    {
      "ch": 3,
      "vpd": [2200, 2250, false],
      "soil": [40, 60, false],
      "use": true
    },
    {
      "ch": 4,
      "vpd": [2200, 2250, false],
      "soil": [40, 60, false],
      "use": true
    }
  ]
}



